@use "../abstract/variable" as *;
@use "../abstract/mixin" as *;
.frame {
  padding: 0;
  transition: all 0.2s;
}

.frame-border-wrap {
  background: linear-gradient(
    to bottom,
    map-get($map: $theme, $key: "primary"),
    map-get($map: $theme, $key: "secondary")
  );
  width: 100%;
  border-radius: 7px;
  padding: 2px;
  &.info {
    background: linear-gradient(
      to right,
      map-get($map: $theme, $key: "tertiary"),
      map-get($map: $theme, $key: "tertiary-v")
    );
  }
}
